import React, { Component, PropTypes } from 'react';

class List extends Component {
  onRender = () => {
    const { gallery } = this.props;

    return gallery.map((image) => {
      return (
        <div key={image.id}>
          {image.title}
          {image.thumbnail ? <img src={`/uploads/${image.thumbnail}`} role="presentation" /> : null }
        </div>
      );
    });
  }

  render() {
    if ( !this.props.gallery ) { return null; }
    return (
      <div>
        {this.onRender()}
      </div>
    )
  }
}

List.propTypes = {
  gallery: PropTypes.array
}

module.exports = List;



