import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';

import List from './List';
import { getImages } from 'actions/gallery';

class Gallery extends Component {
  static store(state) {
    return {
      auth: state.auth,
      gallery: state.gallery
    };
  }

  constructor(props) {
    super(props);

    this.state = { authenticated: this.props.auth.authenticated }
  }

  componentWillMount() {
    const { getImages } = this.props;
    getImages();
  }

  handleClick = () => {
    this.props.router.push('/gallery/upload');
  }

  render() {
    const { authenticated } = this.state;
    if( !this.props.gallery ) { return null; }

    const { children, gallery } = this.props;

    return(
      <div>
        {authenticated ? <button onClick={this.handleClick}>업로드</button> : null }
        {children || <List gallery={gallery} />}
      </div>
    );
  }
}

Gallery.propTypes = {
  children: PropTypes.object
}

module.exports = connect(Gallery.store, { getImages })(Gallery);



